<template>
  <div class="member-box">
    <div class="query-wrapper">
      <div>
        <el-form :inline="true" :model="queryForm" ref="queryForm" label-width="100px">
          <el-form-item label="客户手机号" prop="phone">
            <el-input v-model="queryForm.phone" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="跟进人" prop="flowerName">
            <el-input v-model="queryForm.flowerName" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="客户姓名" prop="customerName">
            <el-input v-model="queryForm.customerName" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
        </el-form>
      </div>
      <div class="query-btn-group mt15">
        <el-button type="primary" @click="queryContent('query', pages)">查询</el-button>
        <el-button @click="reset">重置</el-button>
      </div>
    </div>
    <el-table :data="follows">
      <el-table-column label="跟进时间" >
        <template slot-scope="scope">{{scope.row.gmtCreateTime | solveTime}}</template>
      </el-table-column>
      <el-table-column prop="customerName" label="客户名称" />
      <el-table-column prop="phone" label="客户手机号" />
      <el-table-column prop="staffName" label="跟进人" />
      <el-table-column prop="result" label="跟进结果" />
    </el-table>

    <MemberPagination @callback="queryContent" :total="total" :pages="pages"/>
  </div>
</template>
<script>
  import {Apis} from '@/axios'
  import MemberPagination from '@/components/memberPagination'
  import {Message} from "element-ui";
  import {isEmptyObject} from '@/utils/tools'

  export default {
    name: 'FollowUp',
    data() {
      return {
        queryForm: {
          phone:'',
          flowerName:'',
          customerName:''
        },
        queryFormCopy:{},
        follows: [],
        pages: {
          pageNum: 1,
          pageSize: 10
        },
        total: null
      }
    },
    components:{MemberPagination},
    mounted() {
      let _this = this
      _this.init()
    },

    methods: {
      init() {
        let _this = this
        _this.queryContent('query', _this.pages)
      },
      queryContent(type, _pages) {
        let _this = this
        let {flowerCustomerPage} = Apis.member
        if (type == 'pagination') _this.pages = { ..._this.pages, ..._pages}
        let params = type == 'query' ? {..._this.queryForm} : type == 'pagination' ? {..._this.queryFormCopy}: {}
        if (type == 'query' && !isEmptyObject(_this.queryForm)) {
          params = {...params, pageNum: 1, pageSize:10}
        } else {
          params = _pages ? {...params, ..._pages} : {...params, ..._this.pages}
        }
        flowerCustomerPage(params).then(res => {
          if (res.data.status == 200) {
            let {records, total} = res.data.data || {}
            _this.total = total
            _this.follows = records
          } else Message.error(res.data.msg)
        })
      },
      reset() {
        this.$refs['queryForm'].resetFields()
      },

    }
  }
</script>
<style lang="less" scoped>
  .member-box {
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    .query-btn-group{
      text-align: right;
    }
  }
</style>
